import React, { Component } from 'react';
import Vector from "../ParticlesLoader/Vector";

class CanvasGame extends Component {
  render() {
    return <canvas ref="canvas"/>;
  }

  constructor(props) {
    super(props);
    this.mouseCoordinates = {
      x: 0,
      y: 0
    };
    this.playerCircle = {
      size: 2,
      coordinates: {
        x: 0,
        y: 0
      },
      color: "pink"
    }
  }

  componentDidMount() {
    this.canvas = this.refs.canvas;
    let { canvas } = this.refs;
    this.ctx = canvas.getContext('2d');
    canvas.width = window.innerWidth;
    canvas.height = window.innerHeight;
    this.drawPlayerItem();
    this.addListeners();
    this.loop();
  }

  addListeners() {
    this.canvas.addEventListener("mousemove", this.setMousePosition, false);
  }

  setMousePosition = (e) => {
    this.mouseCoordinates = {
      x: e.clientX,
      y: e.clientY
    };
  };

  loop() {
    this.clear();
    this.printInfo();
    this.update();
    this.draw();
    this.queue();
  }

  printInfo() {
    this.ctx.font = "14px Arial";
    this.ctx.fillStyle = "white";
    this.ctx.textAlign = "left";
    this.ctx.fillText(`${this.canvas.width} x ${this.canvas.height}`, 10, 30);
    this.ctx.fillText(`${this.mouseCoordinates.x} x ${this.mouseCoordinates.y}`, 10, 50);
  }

  clear() {
    this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
  }

  draw() {
    this.drawPlayerItem()
  }

  update() {
    this.recalculatePositions(this.canvas.width, this.canvas.height)
  }

  recalculatePositions = (canvasWidth, canvasHeight) => {

    let { coordinates } = this.playerCircle;

    // let newPosition = new Vector(this.mouseCoordinates.x,this.mouseCoordinates.y);
    // let mag = newPosition.getMagnitude();
    // let angle = newPosition.getAngle();
    // //console.log(Vector.fromAngle(angle,mag));
    // this.playerCircle.coordinates = Vector.fromAngle(angle,mag);

    let field = {
      position: {
        x: this.mouseCoordinates.x,
        y: this.mouseCoordinates.y
      },
      mass: 3
    };

    let vectorX = field.position.x - (coordinates.x || 0);
    let vectorY = field.position.y - (coordinates.y || 0);

    // calculate the force via MAGIC and HIGH SCHOOL SCIENCE!
    let force = field.mass / Math.pow(vectorX * vectorX + vectorY * vectorY, 1.5);

    //console.log(this.mouseCoordinates.y, coordinates,force);

    let newCoordinates = {
      x: vectorX * force,
      y: vectorY * force
    };
    //console.log(newCoordinates);

    this.playerCircle.coordinates.y = newCoordinates.y;
    this.playerCircle.coordinates.x = newCoordinates.x;
    console.log(this.playerCircle.coordinates)
  };

  drawPlayerItem() {
    this.ctx.beginPath();
    this.ctx.arc(
      this.playerCircle.coordinates.x,
      this.playerCircle.coordinates.y,
      this.playerCircle.size,
      0, 2 * Math.PI, true);
    this.ctx.fillStyle = this.playerCircle.color;
    this.ctx.fill();
  }

  queue() {
    window.requestAnimationFrame(() => this.loop());
  }
}

export default CanvasGame;



